body {
  background-color: #ffffff;
  font-family: Merriweather Web;
  font-size: 16px;
  font: #404040;
}

.hero-banner {
  background-size: cover;
  background-repeat: no-repeat;
  height: 100vh;
}

.main-logo {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.content-container h1 {
  font-size: 2.4rem;
  font-weight: 400;
  margin-top: 3.75rem;
  margin-bottom: 3.75rem;
  text-align: center;
  color: #2f1339;
}

.home-product-section {
  display: flex;
  justify-content: center;
  text-align: center;
  height: 270px;
}

.home-product-container {
  border: 1px solid #e1e1e1;
  border-top-width: 5px;
  min-width: fit-content;
  padding: 0.75rem;
  margin: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.home-product-container img {
  height: 70px;
  width: 70px;
  align-self: center;
}

.home-product-container p {
  line-height: 25px;
  width: 245px;
  text-align: center;
  font-size: 19.5px;
}

.home-product-container a {
  padding: 10px 20px;
  color: white;
  font-size: 16px;
  background-color: #248a83;
  text-decoration: none;
  text-transform: uppercase;
}

.home-journal-section {
  display: flex;
  margin-left: auto;
  margin-right: auto;
  width: 1120px;
  padding-bottom: 100px;

  .home-journal-container {
    padding: 16px;
    flex: 0 1 32%;
    position: relative;

    img {
      width: 100%;
      object-fit: cover;
      height: 275px;
    }

    button {
      border: 1px solid black;
      padding: 12px 20px;
      text-decoration: none;
      margin-top: 30px;

      a {
        text-decoration: none;
        color: black;
        font-size: 14px;
        font-weight: 300;
      }
    }
    .home-journal-info {
      span a {
        text-decoration: none;
        text-transform: uppercase;
        font-size: 20px;
        font-weight: bold;
        color: #248a83;
        font-family: Merriweather Web, sans-serif;
      }

      figcaption {
        border: 1px solid #e1e1e1;
        padding: 0.75em;
        border-top-color: lightgrey;
        border-top-width: 5px;

        button:hover {
          background-color: #248a83;
        }
      }
    }

    .home-journal-small {
      font-size: 14px;
      padding-top: 15px;
      padding-bottom: 10px;
    }
  }
}
